import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import { Button, Form, Input } from 'antd';
import styles from './Login.less';

const createForm = Form.create;
const FormItem = Form.Item;

// 
const FormValidator = {
  userExists(rule, value, callback) {
    if (!value) {
      callback();
    } else {
      setTimeout(() => {
        if (value !== 'Admin') {
          callback([new Error('')]);
        } else {
          callback();
        }
      }, 800);
    }
  },
};
// 
class LoginFormBase extends Component {
  constructor(props) {
    super(props);
    this.handleReset = this.handleReset.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleReset(e) {
    e.preventDefault();
    this.props.form.resetFields();
  }
  handleSubmit(e) {
    e.preventDefault();
    this.props.form.validateFields((errors) => {
      if (errors) {
        return;
      }
    });
  }
  render() {
    const { getFieldDecorator, getFieldError, isFieldValidating } = this.props.form;
    return (
      <Form vertical>
        <FormItem
          wrapperCol={{ span: 24 }}
          label=""
          hasFeedback
          help={isFieldValidating('name') ? '...' : (getFieldError('name') || []).join(', ')}
        >
          {getFieldDecorator('name', {
            rules: [
              { required: true, min: 5, message: '' },
              { validator: FormValidator.userExists },
            ],
          })(<Input placeholder="" />)}
        </FormItem>
        <FormItem
          wrapperCol={{ span: 24 }}
          label=""
          hasFeedback
        >
          {getFieldDecorator('password', {
            rules: [{ required: true, whitespace: true, message: '' }],
          })(<Input placeholder="" />)}
        </FormItem>
        <FormItem wrapperCol={{ span: 24 }}>
          <Button style={{ width: '100%' }} type="primary" onClick={this.handleSubmit}>&nbsp;</Button>
        </FormItem>
      </Form>
    );
  }
}
LoginFormBase.propTypes = {
  form: PropTypes.any,
};
// Ant.Formcreate
const LoginForm = createForm()(LoginFormBase);

// 
const Login = () => (
  <div className={`${styles.content} container`}>
    <div className={styles.form}>
      <LoginForm />
      <Link to="/signUp"></Link>
    </div>
  </div>
);

export default Login;



